import { useParams, Link } from "react-router-dom";
import { useContext, useState, useEffect } from "react";
import { StateContext } from "../../StateManager";
import EditableHeader from "./EditableHeader";
import EditableContent from "./EditableContent";
import MaterialIcon from "../../assets/MaterialIcon";
import NoteCreatorHeader from "./NoteCreatorHeader";
import "./style.css";

export function NoteCreator() {
  // main app state manager
  let { state, dispatch } = useContext(StateContext);
  let { noteId } = useParams(); // get the url parameter noteId for determining current note
  const [currentNote, setNote] = useState({}); // creating a local state for working
  // const [currentMode, setMode] = useState("edit"); // state to check whether the window is for
  // viewing or editing [valid options: 'view', 'edit']
  // when component is mounted,
  useEffect(() => {
    // iterate through the existing note
    for (let i = 0; i < state.notes.length; i++) {
      // find the selected note and set it to a state for access
      if (state.notes[i].itemId === Number(noteId)) {
        setNote(state.notes[i]);
      }
    }
  }, [noteId, state.notes]); // dependencies are the main state and the given url parameter

  function submitToMainState() {
    let payload = {
      reqId: Number(noteId),
      changedNote: {
        noteTitle: currentNote.noteTitle,
        contents: currentNote.contents,
        itemId: Number(noteId),
        createdAt: currentNote.createdAt,
      },
    };
    dispatch({ type: "edit", payload: payload });
  }
  // some components
  let leftNav = (
    <Link to="/">
      <MaterialIcon iconId="arrow_back" styling="" />
    </Link>
  );
  let rightNav = (
    <nav>
      <ul className="Options" style={{ display: "flex", cursor: "pointer" }}>
        <li className="Nav-Option" onClick={() => submitToMainState()}>
          <MaterialIcon iconId="check" iconStyle="" />
        </li>
        <li className="Nav-Option">
          <MaterialIcon iconId="dark_mode" iconStyle="" />
        </li>
      </ul>
    </nav>
  );
  return (
    <section className="Note-Creator">
      <NoteCreatorHeader
        leftNav={leftNav}
        rightNav={rightNav}
      ></NoteCreatorHeader>
      <main className="Note-Creator-Main">
        <EditableHeader
          state={currentNote?.noteTitle}
          setState={(val) =>
            setNote({
              noteTitle: val,
              contents: currentNote.contents,
              itemId: currentNote.itemId,
              createdAt: currentNote.createdAt,
            })
          }
        />
        <hr />
        <EditableContent
          state={currentNote?.contents}
          setState={(val) => {
            setNote({
              noteTitle: currentNote.noteTitle,
              contents: val,
              itemId: currentNote.itemId,
              createdAt: currentNote.createdAt,
            });
          }}
        />
      </main>
    </section>
  );
}
