import React from "react";
import "./style.css";

function FloatingActionButton({children, clickHandler, size, color}) {
    let customStyle = {
        backgroundColor: color,
        height: size,
        width: size
    }
    return (
        <button onClick={() => clickHandler()} style={customStyle} className="Floating-Action-Button">
            {children}
        </button>
    )
}
export default FloatingActionButton;