import { NoteItem } from "./NoteItem.js";
import {Link} from "react-router-dom";

export function NoteList({itemsList}) {
    let notes = itemsList.map((item, ind) => (
        <li key={ind}>
            <Link to={`/notes/${item.itemId}`}>
            <NoteItem {...item}></NoteItem>
            </Link>   
        </li>
    ))
    return (
        <ul className="Note-List">
            {notes}
        </ul>
    )
}