import {useReducer, createContext} from "react";

// New Note template
let note_template_title = 'Title';
let note_template_contents = 'Click here to edit note';

function reducer(state, action) {
    switch (action.type) {
        case 'create':
            return {
                notes: [{
                    noteTitle: note_template_title,
                    itemId: state.notes.length,
                    contents: note_template_contents,
                    createdAt: new Date().getTime(),
                },...state.notes]
            };
        case 'remove':
            return state;
        case 'edit':
            // input an id and the changed note
            let id = action.payload.reqId;
            let changedNote = action.payload.changedNote;
            let bufferState = state.notes; // a bufferState to mutate
            for (let note in state.notes) {
                if (state.notes[Number(note)].itemId === id) {
                    // find the specified note
                    let ind = Number(note) // get its index in array
                    bufferState[ind] = changedNote; // use the index and replace the note
                    console.log(bufferState)
                }
            }
            return {notes: bufferState} // setting the state to buffered state
        default:
            throw new Error("No Action Specified.")
    }
}
const StateContext = createContext();

function StateManager({children}) {
    const [state, dispatch] = useReducer(reducer, {
        notes: []
    });
    let val = {
        state, 
        dispatch
    }
    return (
        <StateContext.Provider value={val}>
            {children}
        </StateContext.Provider>
    )
}

export default StateManager;
export {
    StateContext
}