import {useRef, useEffect} from "react";
function EditableHeader({state, setState}) {
	let headerRef = useRef(null);
	useEffect(() => {
		headerRef.current.contentEditable = true;
		headerRef.current.innerText = state;
	})
	let handleChange = (e) => {
		setState(e.target.innerText);
		console.log(e.target.innerText)
	}
	return (
		<header>
			<h1 ref={headerRef} onBlur={e => handleChange(e)}>
			</h1>
		</header>
	)
}
export default EditableHeader;