import { NoteCreator } from "./components/Creator/NoteCreator";
import { BrowserRouter, Route, Switch} from "react-router-dom";
import Home from "./views/Home";
import "./App.css";

function App() {
  return (
    <div className="App">
      <main className="Deck">
        <BrowserRouter>
          <Switch>
            <Route path="/" exact>
              <Home />
            </Route>
            <Route path="/notes/:noteId">
              <NoteCreator></NoteCreator>
            </Route>
          </Switch>
        </BrowserRouter>
      </main>
    </div>
  );
}

export default App;
